import { Vector2D } from "./Vector2D";

export class Particle {
  constructor(mass, charge) {
    this.mass = mass ?? 1;
    this.charge = charge ?? 0;
    this.x = 0;
    this.y = 0;
    this.vx = 0;
    this.vy = 0;
  }

  get pos2D() {
    return new Vector2D(this.x, this.y);
  }

  set pos2D(pos) {
    this.x = pos.x;
    this.y = pos.y;
  }

  get velo2D() {
    return new Vector2D(this.vx, this.vy);
  }

  set velo2D(velo) {
    this.vx = velo.x;
    this.vy = velo.y;
  }
}
